<div class="device"><a class="title" :href="baseUri + device.link">{{ device.description }}</a><div class="thumb"><div class="vendor">{{ device.vendor }}</div><a class="device-img" :href="baseUri + device.link" :style="{ backgroundImage: 'url(' + baseUri + device.image + ')' }"></a></div><div class="desc">{{ device.exposes.join(', ') }}</div></div>