<div class="supported-devices"><div class="filters"><filters v-model="currentFilters"></filters></div><infinite-scroll @load="loadItemsByScroll"><transition-group class="devices-list" tag="div" name="device"><device v-for="device in devicesToShow" :key="device.vendor + device.model" :device="device" @click="storePosition"></device><p v-if="devicesToShow.length === 0" :key="'no-results'">No devices found.</p></transition-group></infinite-scroll></div>