<div class="device-filters"><div class="vendor-filter"><multi-select label="Vendor" :options="vendors" v-model="vendorFilter"></multi-select></div><div class="exposes-filter"><multi-select label="Exposes" :options="exposes" v-model="exposesFilter"></multi-select></div><div class="search-filter"><q-input v-model="searchFilter" filled :debounce="500" type="search" :placeholder="searchPlaceholder" :label="searchFilter || searchPlaceholder === 'type to search' ? 'Search' : 'Press / to search'" @focus="searchPlaceholder = 'type to search'" @blur="searchPlaceholder = 'Press / to search'" clearable><template v-slot:append><q-icon name="info"></q-icon><q-tooltip :offset="[0, 15]" self="bottom middle">RegEx support. E.g you can use<code> (wall|switch)</code> to search for wall OR switch.</q-tooltip></template></q-input></div></div>